$bgColor: rgba(49, 49, 58, 1);
$color: rgba(255, 255, 255, 0.5);
$green: rgb(94, 255, 0);

#projects {
  padding: 30px 0;
}
.projWrapper {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  padding: 10px;
  position: relative;

  .item {
    width: 45%;
    padding: 10px;
  }
  .big {
    width: 65%;
    margin: auto;
  }

  .imgWrap {
    position: relative;
    border-radius: 10px;
    overflow: hidden;
    // border: 5px solid $bgColor;
    // box-shadow: inset 6px 0px 6px 0px rgba(0,0,0,0.13), inset -6px 0px 6px 0px rgba(0,0,0,0.13), 3px 0px 15px 0px rgba(25,218,47,0.55);
    box-shadow: 2px 5px 16px 0px rgb(99, 98, 98), 5px 5px 15px 5px rgb(0, 0, 0);

    // width: 50%;
    &:hover {
      cursor: pointer;
      .overlay {
        height: 100%;
      }
      img {
        filter: none;
      }
    }
  }
  .imgWrap:hover + p {
    color: $green;
  }

  img {
    display: block;
    width: 100%;
    height: auto;
    filter: grayscale(1);
  }

  .overlay {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    // background-color: rgba(94, 255, 0,0.5);
    overflow: hidden;
    width: 100%;
    height: 0;
    transition: 0.5s ease;
  }

  .text {
    color: white;
    font-size: 20px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    text-align: center;
  }
  p {
    text-align: center;
    color: white;
    padding: 15px;
    font-size: 20px;
    font-weight: bold;
  }

  .also {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translate(0, -55%);
    p {
      writing-mode: vertical-rl;
      text-orientation: sideways;

      white-space: pre-line;
    }
    a {
      color: $green;
      &:hover {
        color: $color;
      }
    }
  }
}
